@import '../../../../../styles/templarios.abstracts';

:host {
  display: block;
  width: 100%;
}

.tp-news-item {
  &__container {
    display: flex;
    align-items: center;
    gap: 64px;
    border: 1px solid var(--tp-color-neutral-2);
    border-radius: 8px;
    padding: 40px;
    cursor: pointer;
    background-color: transparent;
    transition: background-color 0.2s ease-in;

    &:hover {
      background-color: var(--tp-color-neutral-2);

      ::slotted([slot='image']) {
        transform: scale(1.15);
      }
    }

    @include breakpoint-down('xl') {
      gap: 48px;
      padding: 32px;
    }

    @include breakpoint-down('lg') {
      gap: 32px;
      padding: 24px;
    }

    @include breakpoint-down('md') {
      gap: 16px;
    }

    @include breakpoint-down('sm') {
      flex-direction: column;
      gap: 24px;
    }
  }

  &__left {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 40%;

    @include breakpoint-down('sm') {
      flex: 1;
    }
  }

  &__image-container {
    display: flex;
    overflow: hidden;
    border-radius: 8px;
  }

  ::slotted([slot='image']) {
    transition: transform 0.3s ease-in;
  }

  &__right {
    display: flex;
    flex-direction: column;
    flex: 60%;

    @include breakpoint-down('sm') {
      flex: 1;
      align-items: flex-start;
      width: 100%;
    }
  }

  ::slotted([slot='category']) {
    --padding-start: 24px !important;
    --padding-end: 24px !important;
    --padding-top: 2px !important;
    --padding-bottom: 2px !important;
    margin-bottom: 32px;
    // TODO fix provis√≥rio padding quando tem os dois plugins juntos
    padding-left: var(--padding-start);
    padding-right: var(--padding-end);
    padding-top: var(--padding-top);
    padding-bottom: var(--padding-bottom);
  }

  ::slotted([slot='title']) {
    margin-bottom: 24px;

    h1 {
      font-size: 32px;
      line-height: 40px;
      font-weight: 600;

      @include breakpoint-down('md') {
        font-size: 24px;
        line-height: 32px;
      }
    }
  }
}
