import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'tp-elem-news-item',
  styleUrl: 'news-item.scss',
  scoped: true,
})
export class TpElemNewsItem {
  render() {
    return (
      <Host class="tp-news-item">
        <div class="tp-news-item__container">
          <div class="tp-news-item__left">
            <div class="tp-news-item__image-container">
              <slot name="image" />
            </div>
          </div>

          <div class="tp-news-item__right">
            <slot name="category" />
            <slot name="title" />
            <slot name="date-time" />
          </div>
        </div>
      </Host>
    );
  }
}
